// Import all of Bootstrap's JS
import * as bootstrap from 'bootstrap';

import { auto, createPopper } from '@popperjs/core';

import AOS from 'aos';
import 'aos/dist/aos.css';

import Swiper from 'swiper';
import Swiper from 'swiper/swiper-bundle.esm.js';

// import 'swiper/css';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.min.css';



AOS.init({
  duration: 1200
});

// ********** fixed navbar ************
/* const navbar = document.getElementById("nav"); */
const topLink = document.querySelector(".top-link");

window.addEventListener("scroll", function () {
  const scrollHeight = window.pageYOffset;
  /* const navHeight = navbar.getBoundingClientRect().height;
  if (scrollHeight > navHeight) {
    navbar.classList.add("fixed-nav");
  } else {
    navbar.classList.remove("fixed-nav");
  } */
  // setup back to top link

  if (scrollHeight > 500) {

    topLink.classList.add("show-link");
  } else {
    topLink.classList.remove("show-link");
  };
});


const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]');
const tooltipList = [...tooltipTriggerList].map(tooltipTriggerEl => new bootstrap.Tooltip(tooltipTriggerEl));

const scrollBn = document.querySelector(".banner_bottom");

const Skewedline_1 = document.querySelector('.skew_line_l');
const Skewedline_2 = document.querySelector('.skew_line_2');

window.addEventListener("scroll", function () {
  const scrollHeight = window.pageYOffset; 
  
  let value1 = -20 + window.scrollY/45;  // transform: skewY(-20deg);
  let value2 = 200 +  window.scrollY/-135;  // transform: skewY(200deg);

  if (scrollHeight > 10) {
    scrollBn.classList.add("bn_show");
  } else {
    scrollBn.classList.remove("bn_show");
  };
  
  Skewedline_1.style.transform = `skewY(${ value1 }deg)`;
  Skewedline_2.style.transform = `skewY(${ value2 }deg)`;

});

// init Swiper:

const swiper = new Swiper('.swiper', {
  // direction: "vertical",
  spaceBetween: 300,
  speed:2000, //  
  centeredSlides: true,
  autoplay: {
    delay: 3000,
    disableOnInteraction: false,
  },
  effect: 'fade',
  fadeEffect: {
    crossFade: true
  },
  slidesPerView: auto,
  loop: true,
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev"
  }
})



